/**
 * A module that defines Text element used to indicate links.
 */

/**
 * ============================================================================
 * IMPORTS
 * ============================================================================
 * @hidden
 */
import { Text, ITextProperties, ITextAdapters, ITextEvents } from "../../core/elements/Text";
import { Sprite, SpriteEventDispatcher, AMEvent } from "../../core/Sprite";
import { List, IListEvents } from "../../core/utils/List";
import { IDisposer, MutableValueDisposer } from "../../core/utils/Disposer";
import { RoundedRectangle } from "../../core/elements/RoundedRectangle";
import { Label } from "../../core/elements/Label";
import { color } from "../../core/utils/Color";
import * as $iter from "../../core/utils/Iterator";
import * as $path from "../../core/rendering/Path";
import { MouseCursorStyle } from "../../core/interaction/Mouse";
import { InterfaceColorSet } from "../../core/utils/InterfaceColorSet";

/**
 * ============================================================================
 * REQUISITES
 * ============================================================================
 * @hidden
 */

/**
 * Defines properties for [[TextLink]].
 */
export interface ITextLinkProperties extends ITextProperties { }

/**
 * Defines events for [[TextLink]].
 */
export interface ITextLinkEvents extends ITextEvents { }

/**
 * Defines adapters for [[TextLink]].
 * 
 * @see {@link Adapter}
 */
export interface ITextLinkAdapters extends ITextAdapters, ITextLinkProperties { }


/**
 * ============================================================================
 * MAIN CLASS
 * ============================================================================
 * @hidden
 */

/**
 * Creates a text element with a link.
 * 
 * @see {@link ITextLinkEvents} for a list of available events
 * @see {@link ITextLinkAdapters} for a list of available Adapters
 */
export class TextLink extends Text {

	/**
	 * Defines available properties.
	 * 
	 * @ignore Exclude from docs
	 * @type {ITextLinkProperties}
	 */
	public _properties: ITextLinkProperties;

	/**
	 * Defines available adapters.
	 * 
	 * @ignore Exclude from docs
	 * @type {ITextLinkAdapters}
	 */
	public _adapter: ITextLinkAdapters;

	/**
	 * Event dispatcher.
	 * 
	 * @type {SpriteEventDispatcher<AMEvent<TextLink, ITextLinkEvents>>} Event dispatcher instance
	 */
	public events: SpriteEventDispatcher<AMEvent<TextLink, ITextLinkEvents>>;

	/**
	 * Constructor
	 */
	constructor() {
		super();

		this.className = "TextLink";
		this.selectable = true;

		let interfaceColors = new InterfaceColorSet();

		this.fill = interfaceColors.getFor("primaryButton");	

		let hoverState = this.states.create("hover");
		hoverState.properties.fill = interfaceColors.getFor("primaryButtonHover");

		let downState = this.states.create("down");
		downState.properties.fill = interfaceColors.getFor("primaryButtonDown");

		this.cursorOverStyle = MouseCursorStyle.pointer;

		this.applyTheme();
	}

}


