/**
 * Modal class is used to display information over chart area.
 */

/**
 * ============================================================================
 * IMPORTS
 * ============================================================================
 * @hidden
 */
import { Adapter } from "../utils/Adapter";
import { BaseObject } from "../Base";
import { interaction } from "../interaction/Interaction";
import { InteractionObject } from "../interaction/InteractionObject";
import { keyboard } from "../utils/Keyboard";
import { MultiDisposer, IDisposer } from "../utils/Disposer";

/**
 * Represents a list of available adapters for Export.
 */
export interface IModalAdapters {

	/**
	 * Applied to the class prefixes.
	 *
	 * @type {string}
	 */
	classPrefix: string,

	/**
	 * Applied to modal content before it is shown.
	 * @type {string}
	 */
	content: string,

	/**
	 * Applied to the screen reader title.
	 * @type {string}
	 */
	readerTitle: string,

	/**
	 * Applied to default `defaultStyles` property before it is rettrieved.
	 *
	 * @type {boolean}
	 */
	defaultStyles: boolean,

	/**
	 * Applied to class names list that are added ass `class` for various modal
	 * elements.
	 */
	classNames: {
		wrapperClass: string,
		contentClass: string,
		curtainClass: string,
		closeClass: string
	}

}

/**
 * Shows an HTML modal which covers window or a chart area.
 *
 * @todo Positioning over whole window
 */
export class Modal extends BaseObject {

	/**
	 * Adapter.
	 *
	 * @type {Adapter<Modal, IModalAdapters>}
	 */
	public adapter: Adapter<Modal, IModalAdapters> = new Adapter<Modal, IModalAdapters>(this);

	/**
	 * A reference to an HTML element to be used for container. If not set, modal
	 * will cover the whole window.
	 *
	 * @type {HTMLElement}
	 */
	public container: HTMLElement | Document;

	/**
	 * A top element in modal.
	 *
	 * @type {HTMLElement}
	 */
	protected _element: HTMLElement;

	/**
	 * A content wrapper element.
	 *
	 * @type {HTMLElement}
	 */
	protected _contentWrapperElement: HTMLElement;

	/**
	 * A content element.
	 *
	 * @type {HTMLElement}
	 */
	protected _contentElement: HTMLElement;

	/**
	 * Holds [[InteractionObject]] representation of modal curtain element.
	 *
	 * @type {InteractionObject}
	 */
	protected _curtainIO: InteractionObject;

	/**
	 * Holds [[InteractionObject]] representation of the close button element.
	 *
	 * @type {InteractionObject}
	 */
	protected _closeIO: InteractionObject;

	/**
	 * Contents of modal window.
	 *
	 * @type {string}
	 */
	protected _content: string = "";

	/**
	 * Prefix to apply to class names for modal elements.
	 *
	 * @type {string}
	 */
	protected _classPrefix: string = "ammodal"

	/**
	 * If set to `true` [[Modal]] will use default styles.
	 *
	 * @type {boolean}
	 */
	protected _defaultStyles: boolean = true;

	/**
	 * A title for screen readers. It is very highly recommended to set that title
	 * so that people using screen reader tools can get an immediate summary of
	 * the information in the modal.
	 *
	 * @type {string}
	 */
	public readerTitle: string = "";

	/**
	 * Is modal closable?
	 *
	 * @type {boolean}
	 */
	private _closable: boolean = true;

	/**
	 * Shows modal window.
	 */
	public show(): void {
		if (this.container) {
			this.container.appendChild(this.element);
			this.positionElement();
		}
	}

	/**
	 * Hides modal window.
	 */
	public hide(): void {
		if (this.element && this.element.parentElement) {
			this.element.parentElement.removeChild(this.element);
		}
	}

	/**
	 * Destroy (dispose) modal.
	 */
	public dispose(): void {
		super.dispose();
		this.hide();
	}

	/**
	 * Creates and returns an HTML holder element for modal (`<div>`).
	 *
	 * @ignore Exclude from docs
	 * @return {HTMLElement} Modal holder element
	 */
	public get element(): HTMLElement {
		if (!this._element) {

			// Get class names for modal elements
			let classNames = this.adapter.apply("classNames", {
				wrapperClass: this.classPrefix + "",
				contentClass: this.classPrefix + "-content",
				curtainClass: this.classPrefix + "-curtain",
				closeClass: this.classPrefix + "-close"
			});

			// Create the wrapper
			let wrapper = document.createElement("div");
			wrapper.className = classNames.wrapperClass;

			// See if we're loading external CSS
			// Hide it until CSS is loaded
			if (this.defaultStyles) {
				wrapper.style.display = "none";
			}

			// Create the curtain
			let curtain = document.createElement("div");
			curtain.className = classNames.curtainClass;

			// Append curtain to wrapper
			wrapper.appendChild(curtain);

			// Create an InteractionObject for curtain because we might need to
			// set interactions on it
			this._curtainIO = interaction.getInteraction(curtain);

			// Create content element
			let contentWrapper = document.createElement("div");
			contentWrapper.className = classNames.contentClass;

			// Create close button
			let close = document.createElement("a");
			close.className = classNames.closeClass;

			// Content div
			let content = document.createElement("div");
			content.innerHTML = this.content;

			// Append close to content wrapper
			contentWrapper.appendChild(close);

			// Create an InteractionObject for close
			this._closeIO = interaction.getInteraction(close);

			// Hide close for now
			close.style.visibility = "hidden";

			// Add accessible stuff
			content.setAttribute("role", "dialog");
			content.setAttribute("aria-label", this.adapter.apply("readerTitle", this.readerTitle));

			// Add to wrapper
			contentWrapper.appendChild(content);
			wrapper.appendChild(contentWrapper);

			// Set references for easy access later on
			this._element = wrapper;
			this._contentElement = content;
			this._contentWrapperElement = contentWrapper;

			// Add IOs to disposers
			this._disposers.push(this._curtainIO);
			this._disposers.push(this._closeIO);

			// Apply events
			this._applyEvents();

			// Load CSS
			if (this.defaultStyles) {
				this.loadDefaultCSS().then((css: any) => {
					this._disposers.push(css.default(this.classPrefix));
					this._element.style.display = "initial";
				});
			}
		}
		return this._element;
	}

	/**
	 * Positions content element in the center of modal based on its actual size.
	 *
	 * @ignore Exclude from docs
	 */
	public positionElement(): void {
		setTimeout(() => {
			this._contentWrapperElement.style.left = "0";
			this._contentWrapperElement.style.top = "0";
			setTimeout(() => {
				let bbox = this._contentWrapperElement.getBoundingClientRect();
				this._contentWrapperElement.style.left = "50%";
				this._contentWrapperElement.style.top = "50%";
				this._contentWrapperElement.style.marginLeft = Math.round(-bbox.width / 2) + "px";
				this._contentWrapperElement.style.marginTop = Math.round(-bbox.height / 2) + "px";
			}, 1);
		}, 1);
	}

	/**
	 * A prefix that is applied to class names of various modal elements.
	 *
	 * @return {string} Class name prefix
	 */
	public get classPrefix(): string {
		return this.adapter.apply("classPrefix", this._classPrefix);
	}

	/**
	 * @param {string} value Class name prefix
	 */
	public set classPrefix(value: string) {
		this._classPrefix = value;
	}

	/**
	 * Modal content.
	 *
	 * Modal contemt can be any valid HTML, including CSS.
	 * @param {string} value Modal content
	 */
	public set content(value: string) {
		this._content = value;
		if (this._contentElement) {
			this._contentElement.innerHTML = value;
			this.positionElement();
		}
	}

	/**
	 * @return {string} Modal content
	 */
	public get content(): string {
		return this.adapter.apply("content", this._content);
	}

	/**
	 * Is modal closable?
	 *
	 * If it is, it can be closed in a number of ways, e.g. by hitting ESC key,
	 * clicking curtain, or clicking the close button.
	 *
	 * If it is not closable, the only way to close it is via `hide()` call.
	 *
	 * @param {boolean} value Closable?
	 */
	public set closable(value: boolean) {
		if (value !== this._closable) {
			this._closable = value;
			this._applyEvents();
		}
	}

	/**
	 * @return {boolean} Closable?
	 */
	public get closable(): boolean {
		return this._closable;
	}

	/**
	 * Should modal use default CSS?
	 *
	 * If default CSS is disabled, an external CSS should handle the look of the
	 * modal, since it will look quite out of place otherwise.
	 *
	 * @default true
	 * @param {string} Use default CSS?
	 */
	public set defaultStyles(value: boolean) {
		if (this._defaultStyles != value) {
			this._defaultStyles = value;
		}
	}

	/**
	 * @return {boolean} Use default CSS?
	 */
	public get defaultStyles(): boolean {
		return this.adapter.apply("defaultStyles", this._defaultStyles);
	}

	/**
	 * Loads modal CSS.
	 *
	 * @ignore Exclude from docs
	 * @return {Promise<any>} Promise
	 */
	public loadDefaultCSS(): Promise<any> {
		return import(/* webpackChunkName: "exportcss" */ "./ModalCSS");
	}

	/**
	 * If modal is closable, this method adds various events to modal elements.
	 */
	protected _applyEvents(): void {
		if (this._closeIO) {
			if (this.closable) {
				this._closeIO.element.style.visibility = "visible";
				this._disposers.push(new MultiDisposer([
					interaction.body.events.on("keyup", (ev) => {
						if (keyboard.isKey(ev.event, "esc") && this.closable) {
							this.hide()
						}
					}),
					this._curtainIO.events.on("hit", (ev) => {
						this.hide();
					}),
					this._closeIO.events.on("hit", (ev) => {
						this.hide();
					})
				]));
			}
			else {
				this._closeIO.element.style.visibility = "hidden";
			}
		}
	}

}



