/**
 * A collection of low-level async operation stuff.
 */

/**
 * ============================================================================
 * IMPORTS
 * ============================================================================
 * @hidden
 */
import * as $array from "./Array";

/**
 * [Listener description]
 *
 * @ignore Exclude from docs
 * @todo Description
 * @type {[type]}
 */
export type Listener = (now: number) => void;

let pendingFrame = false;
let pendingIdle = false;

const nextQueue: Array<Listener> = [];
const readQueue: Array<Listener> = [];
const writeQueue: Array<Listener> = [];
const idleQueue: Array<Listener> = [];


const fps = 1000 / 60;

/**
 * [raf description]
 *
 * @ignore Exclude from docs
 * @todo Description
 * @type {[type]}
 */
export const raf =
	(typeof requestAnimationFrame === "function"
		? function(fn: () => void): void {
			requestAnimationFrame(fn);
		}
		: function(fn: () => void): void {
			setTimeout(fn, fps);
		});

/**
 * [frameLoop description]
 *
 * @ignore Exclude from docs
 * @todo Description
 */
function frameLoop(): void {
	const now = Date.now();


	const length = nextQueue.length;

	for (let i = 0; i < length; ++i) {
		nextQueue[i](now);
	}

	$array.shiftLeft(nextQueue, length);


	for (let i = 0; i < readQueue.length; ++i) {
		readQueue[i](now);
	}

	readQueue.length = 0;


	for (let i = 0; i < writeQueue.length; ++i) {
		writeQueue[i](now);
	}

	writeQueue.length = 0;


	if (nextQueue.length === 0 && readQueue.length === 0) {
		pendingFrame = false;

	} else {
		raf(frameLoop);
	}
}

/**
 * [pendFrame description]
 *
 * @ignore Exclude from docs
 * @todo Description
 */
function pendFrame(): void {
	if (!pendingFrame) {
		pendingFrame = true;

		raf(frameLoop);
	}
}


/**
 * [nextFrame description]
 *
 * @ignore Exclude from docs
 * @todo Description
 * @param {Listener} fn [description]
 */
export function nextFrame(fn: Listener): void {
	nextQueue.push(fn);
	pendFrame();
}

/**
 * [readFrame description]
 *
 * @ignore Exclude from docs
 * @todo Description
 * @param {Listener} fn [description]
 */
export function readFrame(fn: Listener): void {
	readQueue.push(fn);
	pendFrame();
}

/**
 * [writeFrame description]
 *
 * @ignore Exclude from docs
 * @todo Description
 * @param {Listener} fn [description]
 */
export function writeFrame(fn: Listener): void {
	writeQueue.push(fn);
	pendFrame();
}

/**
 * @ignore Exclude from docs
 * @todo Description
 */
interface IdleInfo {
	didTimeout: boolean;
	timeRemaining: () => number;
}

/**
 * @ignore Exclude from docs
 * @todo Description
 */
interface IdleOptions {
	timeout: number;
}

/**
 * [void description]
 *
 * @ignore Exclude from docs
 * @todo Description
 * @type {[type]}
 */
declare const requestIdleCallback: (fn: (info: IdleInfo) => void, options: IdleOptions) => void;

// Polyfill
// https://developer.mozilla.org/en-US/docs/Web/API/Background_Tasks_API#Falling_back_to_setTimeout
const requestIdle: typeof requestIdleCallback =
	(typeof requestIdleCallback === "function"
		? requestIdleCallback
		: function(fn: (info: IdleInfo) => void): void {
			// TODO is this a good polyfill ?
			readFrame(function() {
				const startTime = Date.now();

				fn({
					didTimeout: false,
					timeRemaining: function() {
						return Math.max(0, 50.0 - (Date.now() - startTime));
					}
				});
			});
		});

/**
 * [idleLoop description]
 *
 * @ignore Exclude from docs
 * @todo Description
 * @param {IdleInfo} info [description]
 */
function idleLoop(info: IdleInfo): void {
	const now = Date.now();

	let i = 0;

	// TODO maybe new callbacks should be pushed to the next update tick ?
	while (i < idleQueue.length) {
		if (info.didTimeout || info.timeRemaining() > 0) {
			idleQueue[i](now);
			++i;

		} else {
			$array.shiftLeft(idleQueue, i);
			// TODO is his a good timeout ?
			requestIdle(idleLoop, { timeout: 50 });
			return;
		}
	}

	idleQueue.length = 0;
	pendingIdle = false;
}


/*export function whenIdle(fn: Listener): void {
	idleQueue.push(fn);

	if (!pendingIdle) {
		pendingIdle = true;
		// TODO is his a good timeout ?
		requestIdle(idleLoop, { timeout: 50 });
	}
}*/

/**
 * [whenIdle description]
 *
 * @ignore Exclude from docs
 * @todo Description
 * @param {Listener} fn [description]
 */
export function whenIdle(fn: Listener): void {
	idleQueue.push(fn);
}



/**
 * [triggerIdle description]
 *
 * @ignore Exclude from docs
 * @todo Description
 * @todo Maybe don't trigger a callback which was added while in the middle of triggering?
 */
export function triggerIdle(): void {
	const now = Date.now();

	const length = idleQueue.length;

	for (let i = 0; i < length; ++i) {
		(<any>idleQueue.shift())(now);
	}
}


