/**
 * Contains code and logic for generating radial gradients.
 */

/**
 * ============================================================================
 * IMPORTS
 * ============================================================================
 * @hidden
 */
import { BaseObject } from "../../Base";
import { List } from "../../utils/List";
import { Group } from "../Group";
import { AMElement } from "../AMElement";
import { Paper } from "../Paper";
import { system } from "../../System";
import { IGradientStop } from "./LinearGradient";
import { IPoint } from "../../defs/IPoint";
import { Color, iRGB } from "../../utils/Color";
import * as $iter from "../../utils/Iterator";


/**
 * ============================================================================
 * MAIN CLASS
 * ============================================================================
 * @hidden
 */

/**
 * Radial gradient class.
 */
export class RadialGradient extends BaseObject {

	/**
	 * List of colors switch definitions in a gradient.
	 * 
	 * @type {List<IGradientStop>}
	 */
	protected _stops: List<IGradientStop> = new List<IGradientStop>();

	/**
	 * An SVG `<group>` element used to draw gradient.
	 *
	 * @ignore Exclude from docs
	 * @type {Group}
	 */
	public element: Group;

	/**
	 * Reference to Paper to add element to.
	 * 
	 * @type {Paper}
	 */
	protected _paper: Paper;

	/**
	 * A center coordinates for the radial gradient.
	 * 
	 * @type {IPoint}
	 */
	protected _center: IPoint;

	/**
	 * Focal point coordinates.
	 * 
	 * @type {IPoint}
	 */
	protected _focalPoint: IPoint;

	/**
	 * Constructor
	 */
	constructor() {
		super();
		this.element = this.paper.addGroup("radialGradient");
		this.id = "gradient-" + system.getUniqueId();
		this.element.attr({ "id": this.id });
		this._disposers.push(this.element);
		this.applyTheme();
	}

	/**
	 * Draws gradient.
	 */
	protected draw(): void {

		let gradientElement = this.element;
		if (this._center) {
			gradientElement.attr({ "cx": this._center.x, "cy": this._center.y });
		}

		if (this._focalPoint) {
			gradientElement.attr({ "fx": this._focalPoint.x, "fy": this._focalPoint.y });
		}

		$iter.each($iter.indexed(this._stops.iterator()), (a) => {
			let i = a[0];
			let stop = a[1];
			let offset: number = stop.offset;

			if (isNaN(offset)) {
				offset = i / (this._stops.length - 1)
			}

			let gradientStop: AMElement = this.paper.add("stop");
			gradientStop.attr({ "stop-color": stop.color });
			if (!isNaN(stop.opacity)) {
				gradientStop.attr({ "stop-opacity": stop.opacity });
			}
			if (!isNaN(offset)) {
				gradientStop.attr({ "offset": offset });
			}

			gradientElement.add(gradientStop);
		});
	}

	/**
	 * Adds a color step to the gradient.
	 * 
	 * @param {Color}   color    Color (hex code or named color)
	 * @param {number}  opacity  Opacity (value from 0 to 1; 0 completely transaprent, 1 fully opaque)
	 * @param {number}  offset   Position of color in the gradient (value 0 to 1; 0 meaning start of the gradient and 1 end)
	 */
	public addColor(color: Color, opacity?: number, offset?: number): void {
		this._stops.push({ color: color, opacity: opacity, offset: offset });
		this.draw();
	}

	/**
	 * A [[Paper]] instace to use for the gradient.
	 *
	 * @ignore Exclude from docs
	 * @param {Paper}  paper  Paper
	 */
	public set paper(paper: Paper) {
		if (this._paper != paper) {
			this._paper = paper;
			this.draw();
			paper.appendDef(this.element);
		}
	}

	/**
	 * @ignore Exclude from docs
	 * @return {Paper} Paper
	 */
	public get paper(): Paper {
		if (this._paper) {
			return this._paper;
		}
		return system.ghostPaper;
	}

	/**
	 * Center coordinates of the gradient.
	 *
	 * @ignore Exclude from docs
	 * @param {IPoint}  point  Center point
	 */
	public set center(point: IPoint) {
		this._center = point;
		this.draw();
	}

	/**
	 * Focal point coordinates of the gradient.
	 *
	 * @ignore Exclude from docs
	 * @param {IPoint}  point  Focal point
	 */
	public set focalPoint(point: IPoint) {
		this._focalPoint = point;
		this.draw();
	}

}


